import React from "react";
import {connect} from "react-redux";
import { Redirect } from 'react-router';
import PropTypes from 'prop-types'
let Validate = require('validate');
Validate = new Validate();
var jwt = require('jwt-simple');
var key = require('key');

class Register extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			errorLastName: '',
			errorFistName: '',
			errorEmail : '',
			errorUserName:'',
			errorPassword:'',
			errorOldPassword:'',
			submitError : []
		}
	}
	registerSubmit(e) {
		/****  S kin ng k  ****/
		e.preventDefault();
		var {dispatch} = this.props;
		dispatch({type: 'ON_LOADING'});
		var $this = this;
		var error = [];
		let lastname = this.refs.lastname.value;
		if(Validate.isRequired(lastname) == false){
			error.push(<p key="lastname">Bn cha nhp H</p>)
		}

		let fistname = this.refs.fistname.value;
		if(Validate.isRequired(fistname) == false){
			error.push(<p key="fistname">Bn cha nhp Tn</p>)
		}

		let email = this.refs.email.value;
		if(Validate.isRequired(email) == false){
			error.push(<p key="email">Bn cha nhp Email</p>)
		}else if(Validate.isEmail(email) == false){
			error.push(<p key="email">nh dng Email khng chnh xc</p>)
		}

		let username = this.refs.username.value;
		if(Validate.isRequired(username) == false){
			error.push(<p key="username">Bn cha nhp Ti khon</p>)
		}else if(Validate.isUsername(username) == false){
			error.push(<p key="username">Ti khon khng c cha u hoc k t c bit</p>)
		}else if(Validate.isMin(username,6) == false){
			error.push(<p key="username">Ti khon phi nhiu hn 6 k t</p>)
		}else if(Validate.isMax(username,100) == false){
			error.push(<p key="username">Ti khon phi t hn 100 k t</p>)
		}

		let password = this.refs.password.value;
		if(Validate.isRequired(password) == false){
			error.push(<p key="password">Bn cha nhp mt khu</p>)
		}else if(Validate.isMin(password,6) == false){
			error.push(<p key="password">Mt khu phi nhiu hn 6 k t</p>)
		}else if(Validate.isMax(password,100) == false){
			error.push(<p key="password">Mt khu khng c qu 100 k t</p>)
		}

		let old_password = this.refs.old_password.value;
		if(Validate.isRequired(old_password) == false){
			error.push(<p key="old_password">Bn cha xc nhn mt khu</p>)
		}else if(Validate.isEqual(old_password,password) == false){
			error.push(<p key="old_password">Mt khu xc nhn khng chnh xc</p>)
		}
		if(error.length == 0){
			let data = {
				'fistname' : this.refs.fistname.value,
				'lastname' : this.refs.lastname.value,
				'email' : this.refs.email.value,
				'username' : this.refs.username.value,
				'password' : this.refs.password.value,
				'old_password' : this.refs.old_password.value
			}
			$.ajax({
				type:"POST",
				url:"/api/register",
				data:data,
				success:function(res){
					if(res.status == 1){
						localStorage.setItem("token", res.token);
						let user = jwt.decode(res.token, key);
						dispatch({type: 'LOG_IN', user: user});
						dispatch({type: 'OFF_LOADING'});
						if(res.token){
							return (
						       <Redirect push to="/"/>
							)
						}
					}else{
						$this.setState({submitError: 'ng k tht bi, vui lng kim tra li'});
						dispatch({type: 'OFF_LOADING'});
					}
				}
			})
		}else{
			$this.setState({submitError:error});
			dispatch({type: 'OFF_LOADING'});
		}
	}
	onKeyUpLastName(){
		let $this = this;
		let value = this.refs.lastname.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorLastName: 'Bn cha nhp H'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorLastName: ''
				};
			})
		}
	}
	onKeyUpFistName(){
		let $this = this;
		let value = this.refs.fistname.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorFistName: 'Bn cha nhp Tn'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorFistName: ''
				};
			})
		}
	}
	onKeyUpEmail(){
		let $this = this;
		let value = this.refs.email.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorEmail: 'Bn cha nhp Email'
				};
			})
		}else if(Validate.isEmail(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorEmail: 'Vui lng nhp ng a ch Email'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorEmail: ''
				};
			})
		}
	}
	isExistUserName(value){
		$.ajax({
			type:"POST",
			url:"/api/register/check",
			data: {
				'username' : value
			},
			success: function(res){
				if(res.status == 1){
					return false;
				}else{
					return true;
				}
			}
		});
	}
	onKeyUpUserName(){
		let $this = this;
		let value = this.refs.username.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorUserName: 'Bn cha nhp Ti khon!'
				};
			})
		}else if(Validate.isUsername(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorUserName: 'Ti khon khng c cha u hoc k t c bit!'
				};
			})
		}else if(Validate.isMin(value,6) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorUserName: 'Ti khon phi di hn 6 k t'
				};
			})
		}else if(Validate.isMax(value,100) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorUserName: 'Ti khon khng c di hn 100 k t'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorUserName: ''
				};
			})
		}
	}
	onKeyUpPassword(){
		let $this = this;
		let value = this.refs.password.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorPassword: 'Bn cha nhp Mt khu!'
				};
			})
		}else if(Validate.isMin(value,6) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorPassword: 'Mt khu phi di hn 6 k t'
				};
			})
		}else if(Validate.isMax(value,100) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorPassword: 'Mt khu khng c di hn 100 k t'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorPassword: ''
				};
			})
		}
	}
	onKeyUpOldPassword(){
		let $this = this;
		let value = this.refs.old_password.value;
		let value2 = this.refs.password.value;
		if(Validate.isRequired(value) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorOldPassword: 'Bn cha xc nhn mt khu!'
				};
			})
		}else if(Validate.isEqual(value,value2) == false){
			$this.setState((prevState, props) => {
			  	return {
			  		errorOldPassword: 'Xc nhn mt khu khng chnh xc!'
				};
			})
		}else{
			$this.setState((prevState, props) => {
			  	return {
			  		errorOldPassword: ''
				};
			})
		}
	}
	componentDidMount(){
		let {dispatch} = this.props;
		dispatch({type:'OFF_POPUP_LOGIN'});
	}
	render() {
		if(localStorage.getItem('token')){
			return (
		       <Redirect push to="/"/>
			)
		}
		return (
			<div className="register">
				<div className="container-fluid">
					<div className="row">
						<div className="col-8 left">
							<h3>To ti khon mi</h3>
							<div className="messageError">
							{this.state.submitError != '' ? this.state.submitError : ''}
							</div>
							<form onSubmit={this.registerSubmit.bind(this)}>
								<div className="group">
									<div className="row">
										<div className="col-6">
											<label>
												H <span>*</span>
												{
													this.state.errorLastName != ''
													?<small>{this.state.errorLastName}</small> 
													: ''
												}
											</label>
											<input 
												className={
													this.state.errorLastName != ''
													? 'error'
													: ''
												}
												type="text"
												ref="lastname"
												onKeyUp={this.onKeyUpLastName.bind(this)}
											/>
										</div>
										<div className="col-6">
											<label>
												Tn <span>*</span>
												{
													this.state.errorFistName != ''
													?<small>{this.state.errorFistName}</small> 
													: ''
												}
											</label>
											<input
												className={
													this.state.errorFistName != ''
													? 'error'
													: ''
												}
												type="text"
												ref="fistname"
												onKeyUp={this.onKeyUpFistName.bind(this)}
											/>
										</div>
									</div>
								</div>
								<div className="group">
									<label>
										Email <span>*</span>
										{
											this.state.errorEmail != ''
											?<small>{this.state.errorEmail}</small> 
											: ''
										}
									</label>
									<input
										className={
											this.state.errorEmail != ''
											? 'error'
											: ''
										}
										type="email"
										ref="email"
										onKeyUp={this.onKeyUpEmail.bind(this)}
									/>
								</div>
								<div className="group">
									<label>
										Ti khon <span>*</span>
										{
											this.state.errorUserName != ''
											?<small>{this.state.errorUserName}</small> 
											: ''
										}
									</label>
									<input
										className={
											this.state.errorUserName != ''
											? 'error'
											: ''
										}
										type="text"
										ref="username"
										onKeyUp={this.onKeyUpUserName.bind(this)}
									/>
								</div>
								<div className="group">
									<label>
										Mt khu <span>*</span>
										{
											this.state.errorPassword != ''
											?<small>{this.state.errorPassword}</small> 
											: ''
										}
									</label>
									<input 
										className={
											this.state.errorPassword != ''
											? 'error'
											: ''
										}
										type="password"
										ref="password"
										onKeyUp={this.onKeyUpPassword.bind(this)}
									/>
								</div>
								<div className="group">
									<label>
										Nhp li mt khu <span>*</span>
										{
											this.state.errorOldPassword != ''
											?<small>{this.state.errorOldPassword}</small> 
											: ''
										}
									</label>
									<input
										className={
											this.state.errorOldPassword != ''
											? 'error'
											: ''
										}
										type="password"
										ref="old_password"
										onKeyUp={this.onKeyUpOldPassword.bind(this)}
									/>
								</div>
								<div className="group">
									<button >ng k</button>
								</div>
							</form>
						</div>
						<div className="col-4 right">
							<h3>iu khon s dng</h3>
							<p>1, Cung cp y  thng tin v chnh xc.</p>
							<p>2, Nhng mc c u <span>*</span> l nhng mc bt buc bn phi cung cp.</p>
							<p>3, Nu thng tin  b ng k xin vui lng lin h vi chng ti <a href="#">Ti y</a>.</p>
							<p>4, Sau khi ng k bn s c cp mt ti khon v chng ti khuyn khch bn khng a thng tin ti khon cho bt k ai.</p>
							<p>5, Thng tin ti khon ca bn s c bo mt tuyt i nu bn mt ti khon th hy bo vi chng ti ngay  ly li.</p>
						</div>
					</div>
				</div>
			</div>
		)
	}
}
module.exports = connect(function (state) {
	return {
		Loading : state.Loading
	}
})(Register);



